<template>
  <Table :data="filteredData">
    <slot></slot>
  </Table>
  <Pagination
    v-model:page="page"
    v-model:limit="limit"
    :data="tableOptions"
  ></Pagination>
</template>

<script>
import { ref, computed, defineComponent, watch } from 'vue'
import Pagination from 'components/Pagination.vue'
export default defineComponent({
  components: {
    Pagination
  },
  props: {
    page: {
      type: Number,
      default: 1
    },
    limit: {
      type: Number,
      default: 10
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:page', 'update:limit'],
  setup(props, { emit }) {
    const page = ref(1)
    const limit = ref(10)

    const totalEntries = computed(() => props.data.length)
    const totalPages = computed(() =>
      Math.ceil(totalEntries.value / limit.value)
    )
    const lastPage = computed(() => totalPages.value)
    const tableOptions = computed(() => {
      return {
        from: limit.value * page.value - limit.value + 1,
        to:
          limit.value * page.value > totalEntries.value
            ? totalEntries.value
            : limit.value * page.value,
        last_page: lastPage.value,
        total: totalEntries.value
      }
    })
    const filteredData = computed(() =>
      props.data.slice(tableOptions.value.from - 1, tableOptions.value.to)
    )
    watch(
      () => ({ page: props.page, limit: props.limit }),
      ({ page: newPage, limit: newLimit }) => {
        page.value = newPage
        limit.value = newLimit
      },
      {
        deep: true
      }
    )
    return {
      page,
      limit,
      totalPages,
			filteredData,
      tableOptions
    }
  }
})
</script>
